  font-size: 60px;
  line-height: 150%;
  font-weight: 700;
  color: #103aae;
}
.tab__item-wrapper b::before {
  content: attr(data-parent);
  position: absolute;
  top: 23%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  font-size: 100px;
  line-height: 150%;
  font-weight: 700;
  font-family: "Roboto Condensed", "Arial", sans-serif;
