.trainer__swiper {
  position: relative;
  width: 93.11%;
  max-width: 1272px;
  margin: 0 auto;
  padding-top: 58px;
  padding-bottom: 96px;
}

.trainer__swaper-slides {
  width: 92%;
}

.trainer__container h2 {
  font-size: 54px;
  line-height: 65px;
  font-weight: 600;
  letter-spacing: 0.03em;
  margin: 0;
}

.trainer__swiper-button {
  border: none;
  background-color: transparent;
  width: 16px;
  height: 32px;
  padding: 0;
  z-index: 3;
  cursor: pointer;
}
.trainer__swiper-button::before, .trainer__swiper-button::after {
  display: none;
}
.trainer__swiper-button--prev {
  left: 0;
  top: 52%;
  transform: rotate(180deg);
}
.trainer__swiper-button--next {
  right: 0;
  top: 52%;
  transform: rotate(360deg);
}
.trainer__swiper-button:focus-visible {
  outline-offset: 2px;
  outline: 1px solid #102654;
}
.trainer__swiper-button:hover {
  opacity: 0.8;
}
.trainer__swiper-button:active {
  opacity: 0.5;
}

.card-trainer {
  position: relative;
  width: 22.44%;
  max-height: 294px;
  height: 24vw;
  margin-right: 40px;
  overflow: hidden;
}

.card-trainer:hover .card-trainer__content {
  z-index: 1;
  background-color: rgba(16, 38, 84, 0.89);
}

.card-trainer__front {
  position: relative;
}

.card-trainer__front h3 {
  font-size: 18px;
  line-height: 150%;
  font-weight: 700;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  margin: 0;
  position: absolute;
  bottom: 30px;
  left: 35px;
}

.card-trainer__images img {
  object-fit: cover;
  width: 100%;
  height: 22vw;
  max-height: 294px;
}

.card-trainer__content {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  padding: 58px 37px;
  overflow: auto;
}

.card-trainer__content h3,
span,
.card-trainer__item-description {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
}

.card-trainer__content h3 {
  font-size: 18px;
  line-height: 150%;
  font-weight: 700;
  margin: 0;
  margin-bottom: 10px;
}

.card-trainer__content span {
  font-size: 16px;
  line-height: 150%;
  font-weight: 400;
  color: #b6bdcc;
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 45px;
}

.card-trainer__item-description {
  font-size: 14px;
  line-height: 150%;
  font-weight: 400;
  padding-left: 16px;
  position: relative;
}
.card-trainer__item-description::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #ffffff;
}
.card-trainer__item-description::before:not(:last-child) {
  margin-bottom: 24px;
}

